.commentContainers {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    rgb(131, 137, 148, 0.5),
    rgb(19, 34, 75, 0.5),
    rgb(9, 24, 82)
  );
  padding: 30px;
}

#titleContainer {
  display: flex;
  justify-content: center;
  gap: 20px;
}

#titleContainer h1 {
  padding: 20px;
  font-size: 2.5rem;
  background-color: rgb(25, 32, 54);
  border-radius: 30px;
}

#titleContainer button {
  background-color: rgb(25, 32, 54);
  width: 300px;
  border-radius: 30px;
  font-size: 1.5rem;
  font-weight: 800;
  padding: 10px;
  color: rgb(121, 122, 138);
}

.contentBlogs {
  width: 100%;
  height: 60vh;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 10px;
}

.commentBlogs {
  border-radius: 30px;
  width: 300px;
  height: min-content;
  background-color: white;
  overflow-wrap: break-word;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  overflow: hidden;
  animation: opacity 3s;
  cursor: pointer;
  transition: 0.2s ease-in;
}
.commentBlogs:hover {
  transform: scale(1.1);
}
.commentName {
  font-size: 1.2rem;
  text-align: left;
  font-weight: 600;
  letter-spacing: 1.2px;
}

.commentName p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 20px;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
