.MainContentContainer {
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    rgb(131, 137, 148, 0.5),
    rgb(19, 34, 75, 0.5),
    rgb(9, 24, 82)
  );
  padding: 30px;
}

.titleContainer {
  width: 80%;
  margin: auto;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: start;
}

.titleContainer h1 {
  border-radius: 20px;
  width: 300px;
  background-color: rgb(25, 32, 54);
  font-weight: 800;
  text-align: center;
  color: rgb(121, 122, 138);
  font-size: 2rem;
  animation: opacity 3s;
}

.contentParagraphContainer {
  width: 100%;
  height: 50vh;
}
.contentParagraphContainer p {
  margin: auto;
  width: 80%;
  text-align: justify;
  font-size: 1.5rem;
  line-height: 2.5rem;
  font-weight: 700;
  color: rgb(10, 15, 83);
  animation: opacity 5s;
}
.content-title {
  font-size: 1.6rem;
}
.contentParagraphContainer p span {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 3px;
  color: rgb(114, 0, 0);
}

.contentParagraphContainer p span a {
  font-size: 1.5rem;
}

/* About  */

#AboutContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}

#AboutContent ul {
  display: flex;
}

#AboutContent ul li {
  display: none;
}
#AboutContent ul li.active {
  flex: 1 0 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  animation: opacity 5s;
}

#AboutContent ul li.active .content-title {
  font-size: 1.6rem;
  font-weight: 800;
}

#AboutContent ul li.active p {
  height: 300px;
}
#pageCount {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
#dotsLi {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background-color: rgb(56, 55, 66);
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
#dotsLi.active {
  transform: scale(1.2);
  background-color: rgb(13, 8, 80);
}
/* Work History */
#work-history {
  width: 500px;
}

#workHistoryContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#workHistoryContainer .ImageCards {
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  background-color: rgba(1, 1, 27, 0.6);
  border-radius: 30px;
  overflow: hidden;
  position: relative;
  transition: 0.3s ease-in;
  animation: toTheRight 3s;
  cursor: pointer;
}
#workHistoryContainer .ImageCards:hover {
  transform: scale(1.1);
}
#workHistoryContainer .ImageCards::after {
  content: "Click Me";
  color: white;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  inset: 0;
  position: absolute;
  background: linear-gradient(
    rgb(131, 137, 148, 0.6),
    rgba(30, 31, 36, 0.9),
    rgba(14, 16, 27, 0.9)
  );
  transition: 0.6s ease-in-out;
}
#workHistoryContainer .ImageCards:hover::after {
  transform: translateY(100%);
}

.imageHolder {
  height: 300px;
}
.imageHolder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#workHistoryContainer .ImageCards h1 {
  padding: 10px 0;
  font-size: 1.6rem;
  color: rgb(198, 199, 214);
  letter-spacing: 0;
}
#workHistoryContainer .ImageCards p {
  line-height: 1.5rem;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  color: white;
  margin: 0;
}

/* Comment */

@keyframes toTheRight {
  from {
    transform: translateX(50%);
  }
  to {
    transform: translateX(0%);
  }
}
