.ModalContainer {
  position: absolute;
  inset: 0;
  background-color: rgb(0, 0, 0, 0.9);
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: levelColor 1s;
}
.modal-info {
  opacity: 0;
  transition: 0.5s ease-in;
}
.modal-info.active {
  width: 40%;
  height: 80vh;
  background-color: rgb(201, 197, 197);
  border-radius: 30px;
  animation: toThetop 1s;
  display: flex;

  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  gap: 20px;
  opacity: 1;
}

.modal-info.active h1 {
  font-size: 1.6rem;
  text-transform: uppercase;
}
.modal-info.active .image {
  width: 80%;
  height: 400px;
  background-color: red;
}
.modal-info.active p {
  font-size: 1.2rem;
  font-weight: 700;
  color: rgb(70, 68, 68);
  text-align: justify;
}

.modal-info.active p:nth-child(4) {
  width: 80%;
}
@keyframes levelColor {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes toThetop {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0%);
  }
}
