.RightSectionContainer {
  width: 30%;
  height: 95vh;
  background: linear-gradient(
    rgb(59, 63, 70),
    rgb(26, 42, 87),
    rgb(10, 20, 61)
  );

  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px 0px;
  border-radius: 30px;
  box-shadow: 1px 10px 200px rgba(87, 87, 119, 0.5);
  animation: toTheLeft 1s;
}

h1 {
  color: rgb(138, 137, 153);
  letter-spacing: 2px;
}

.image {
  width: 300px;
  height: 300px;
  border-radius: 30px;
  overflow: hidden;
}
.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.name {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.name h1 {
  font-size: 2rem;
}
.contact h1 {
  text-align: left;
}
.contact ul {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 10px;
}

.contact-list {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 5rem;
  list-style-type: none;
}

.contact-list span {
  font-size: 1.2rem;
  overflow-wrap: break-word;
}
.contactLabel {
  width: 30%;
  display: flex;
  gap: 10px;
}
.contactLabel span:nth-child(2) {
  width: 100px;
  text-align: left;
}

.user-container {
  width: 40%;
  text-align: left;
}

.contact {
  width: 80%;
  height: 20vh;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact h1 {
  width: 80%;
  text-align: left;
  font-size: 2rem;
  text-transform: uppercase;
}

.onlineProfiles {
  display: flex;
  width: 100%;
  height: 100px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 10px;
}
.onlineProfiles h1 {
  width: 80%;
  text-align: left;
}

.onlineProfiles ul {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.onlineProfiles ul li {
  width: 100%;
  list-style-type: none;
  text-align: left;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  gap: 5px;
}

.onlineProfiles ul a {
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

.skills {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}
.skills h1 {
  width: 80%;
  height: 60px;
  font-size: 1.8rem;
  text-align: left;
}
.skillsContainer {
  width: 100%;
}
.skillsContainer ul {
  margin: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}
.skillsContainer ul li {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: 0.3s ease-in;
}

.skillsContainer ul li:hover {
  transform: scale(1.2);
  background-color: rgb(37, 37, 51);
  border-radius: 30px;
}

.skillsContainer ul li span:nth-child(1) {
  display: flex;
  align-items: center;
  width: 200px;
  font-size: 1.2rem;
}
.skillsContainer ul li span:nth-child(3) {
  font-size: 1.2rem;
  font-weight: 800;
}

.levelContainer {
  width: 50%;
  background-color: rgb(65, 65, 68);
  height: 20px;
  border-radius: 30px;
}

.levelColor {
  background-color: rgb(36, 77, 189);
  border-radius: 30px;
  animation: levelColor 5s;
}

@keyframes levelColor {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes toTheLeft {
  from {
    transform: translateX(-50%);
  }
  to {
    transform: translateX(0%);
  }
}
