.LeftMainContainerMain {
  width: 60%;
  height: 80vh;
  background-color: black;
  border-radius: 30px;
  box-shadow: 1px 10px 200px rgba(87, 87, 119, 0.5);
  overflow: hidden;
  animation: toTheRight 3s;
}
.header {
  width: 100%;
  height: 80px;
  background-color: rgb(45, 44, 53);
  display: flex;
  align-items: center;
}

.mainContent {
  width: 100%;
  background-color: white;
  height: 100%;
  display: flex;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.mainContent li {
  flex: 1 0 100%;
  list-style-type: none;
}

.elementContainer {
  display: none;
  transition: 0.5s ease-in;
}

.elementContainer.active {
  display: block;
  width: 100%;
  animation: fadeIn 3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes toTheRight {
  from {
    transform: translateX(50%);
  }
  to {
    transform: translateX(0%);
  }
}
