.NavBarContainer {
  width: 100%;
}

.NavBarContainer ul {
  margin: auto;
  width: 80%;
  height: 40px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-between;
}

.navBars {
  width: 250px;
  color: white;
  font-size: 1.8rem;
  list-style: none;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s ease-in;
}

.navBars.active {
  transform: scale(1.2);
  font-weight: 800;
  color: rgb(3, 11, 56);
  background-color: rgb(160, 153, 153);
}

@media only screen and (max-width: 2254px) {
  .navBars {
    font-size: 1.2rem;
  }
}
