.addComent {
  position: absolute;
  inset: 0;
  background-color: rgb(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.addComent form {
  border-radius: 30px;
  width: 600px;
  height: 50vh;
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}

.formContainer {
  height: 30vh;
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 80%;
}
.buttonContainer {
  width: 100%;
  height: 80px;
}
.buttonContainer button {
  width: 200px;
  height: 80px;
  font-size: 1.8rem;
  border-radius: 30px;
  cursor: pointer;
}

.addComent form label {
  font-size: 1.2rem;
  font-weight: 800;
  width: 100%;
  text-align: left;
}

.addComent form input {
  width: 80%;
  height: 40px;
  border-radius: 30px;
  padding: 0 30px;
  font-size: 1.2rem;
  font-weight: 700;
}

.textArea-container {
  width: 100%;
}
.textArea-container p {
  line-height: 15px;
  text-align: right;
  padding: 0 20px;
  font-weight: 700;
}

.addComent form textarea {
  padding: 20px 30px;
  border-radius: 30px;
  width: 100%;
  height: 200px;
  border-radius: 30px;

  font-size: 1.2rem;
  font-weight: 700;
  overflow: hidden;
  resize: none;
}
