.homepageContainer {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  position: relative;
  isolation: isolate;
}

.bgContainer {
  inset: 0;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: -1;
}

video {
  margin: 0;
  object-fit: cover;
  object-position: center;

  width: 100%;
  height: 100vh;
  inset: 0;
}

.frontEndDeveloperContainer {
  color: white;

  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.frontEndDeveloperContainer h1 {
  width: 100%;
  font-size: 4rem;
  display: flex;
  align-items: center;
  gap: 30px;
  font-weight: 800;
  justify-content: center;
  color: rgb(96, 96, 240);
  text-shadow: 10px 10px 200px rgb(30, 30, 145);
}

.frontEndDeveloperContainer span {
  font-size: 2rem;
  text-transform: uppercase;
  color: white;
}
.frontEndDeveloperContainer h2 {
  width: 100%;
  font-size: 4rem;
  text-align: center;
  font-weight: 600;
  letter-spacing: 4px;
  line-height: 100px;
}

.btnContent {
  width: 300px;
  height: 60px;
  border-radius: 30px;
  margin-top: 5rem;
  font-size: 3rem;
  text-decoration: none;
  color: black;
  font-weight: 800;
  font-size: 1.8rem;
  cursor: pointer;
}
.btnContent a {
  text-decoration: none;
}
